import React, { useState, useEffect } from "react";
import CalendarHeader from "../CalendarHeader/CalendarHeader";
import Day from "../Day/Day";
import classes from "./calendar.module.css";
import { useDate } from "../../../hooks/useDate";

// dispatcher
import { useDispatch } from "react-redux";

// request helpers
import { FetchEventsForMonth } from "../../../api/Events/Business";

// reducers actions imports
import { addUserEvent } from "../../../Store/calendarDashboard/events/eventSlice";

// date formatter utils imports
import { formatDate } from "../../../utils/calendarDashboard";

// posts requests helpers
import { FetchBusinessPostsForMonth } from "../../../api/Posts";

// strategy requests helpers
import { FetchStrategy } from "../../../api/PostStrategy";

// posts actions imports
import { addPost } from "../../../Store/calendarDashboard/posts/postSlice";

// contexts
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useBusiness } from "../../../contexts/BusinessProvider";

const Calendar = () => {
  const [nav, setNav] = useState(0);

  const [events] = useState(
    localStorage.getItem("events")
      ? JSON.parse(localStorage.getItem("events"))
      : []
  );

  const { setErrorMessage } = useToastMessage();

  const { selectedBusiness } = useBusiness();

  const { paddingDays, days, dateDisplay } = useDate(events, nav);

  // dispatcher
  const dispatch = useDispatch();

  useEffect(() => {
    if (selectedBusiness && selectedBusiness.id) {
      const monthNames = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ];

      const [month, year] = dateDisplay.split(" ");

      let monthIndex = null;

      monthIndex = monthNames.findIndex((m) => m === month);

      if (monthIndex !== null && year) {
        // Fetch all the events and display in calendar + drawer
        FetchEventsForMonth(monthIndex, year, paddingDays)
          .then((data) => {
            const eventsData = data.events.map((event) => {
              const eventData = {};

              let { date, day } = getDayDate(event.eventdate);

              let [month, dayNum] = day.split("-");

              if (dayNum < 10) {
                dayNum = dayNum.replace(/0/g, "");
                day = `${month}-${dayNum}`;
              }

              if (event.isGlobal) {
                eventData.isGlobal = true;
              } else {
                eventData.isManual = true;
              }

              eventData.day = day;
              eventData.date = date;

              eventData.event = {
                name: event.eventName,
                desc: event.description,
              };

              return eventData;
            });

            dispatch(addUserEvent({ type: "FETCH", eventsData }));
          })
          .catch((error) => {
            const { message, error: err } = error;

            if (message) {
              setErrorMessage(message);
            } else if (err) {
              setErrorMessage(err);
            } else {
              setErrorMessage(error);
            }
          });

        FetchBusinessPostsForMonth(monthIndex, year, paddingDays).then(
          async (data) => {
            console.log("hii");
            const { BusinessPosts } = data.FetchPostsData;

            if (BusinessPosts && BusinessPosts.length) {
              const postsListData = await Promise.all(
                BusinessPosts.map(async (post) => {
                  try {
                    // const data = await FetchStrategy(businessID, post.strategyID);

                    const data = await FetchStrategy(post.strategyID);

                    const [postDate] = post.scheduleDateTime.split("T");

                    const dt = new Date(post.scheduleDateTime);

                    const [postTime] = dt
                      .toLocaleTimeString("en-US", {
                        hour: "2-digit",
                        minute: "2-digit",
                      })
                      .split(" ");

                    if (!data.strategy) {
                      return;
                    }

                    const strategyData = {
                      color: data.strategy.color,
                      desc: data.strategy.description,
                      name: data.strategy.name,
                    };

                    let postSubtitle = `Scheduled for ${postTime}`;
                    let showSuccessIcon = false;

                    if (post.platform_post_ids) {
                      const { fb, ig, tw, li } = post.platform_post_ids;

                      if (fb && ig && tw && li) {
                        postSubtitle = `Posted at ${postTime}`;
                        showSuccessIcon = true;
                      }
                    }

                    const dispatchPost = {
                      postID: post._id,
                      postDetails: {
                        ...post,
                        postImage: post.assetURL,
                        postThumbnail: post.thumbnailURL,
                        postDate,
                        postTime,
                        postDesc: post.content,
                        postSubtitle,
                        showSuccessIcon,
                      },

                      event: {
                        day: formatDate(post.scheduleDateTime).split("T")[0],
                        strategy: strategyData,
                        isDraft: false,
                      },

                      post,
                    };

                    return dispatchPost;
                  } catch (error) {
                    console.log("Fetch strategy error: ", error);

                    const { message, error: err } = error;

                    if (message) {
                      setErrorMessage(message);
                    } else if (err) {
                      setErrorMessage(err);
                    } else {
                      setErrorMessage(error);
                    }
                  }
                })
              );

              if (postsListData.length && !postsListData.includes(undefined)) {
                dispatch(addPost({ type: "FETCH", posts: postsListData }));
              }
            }
          }
        );
      }
    }
  }, [selectedBusiness, dateDisplay]);

  // events hook

  useEffect(() => {
    localStorage.setItem("events", JSON.stringify(events));
  }, [events]);

  const WeekDays = () => (
    <div id="weekdays" className={classes.weekDays}>
      <div className={classes.weekDay}>Sun</div>
      <div className={classes.weekDay}>Mon</div>
      <div className={classes.weekDay}>Tue</div>
      <div className={classes.weekDay}>Wed</div>
      <div className={classes.weekDay}>Thu</div>
      <div className={classes.weekDay}>Fri</div>
      <div className={classes.weekDay}>Sat</div>
    </div>
  );

  const Days = () => (
    <div id="calendar" className={classes.dayWrapper}>
      {days.map((d, index) => (
        <Day key={index} day={d} index={index} />
      ))}
    </div>
  );

  const handleNext = () => {
    setNav((prev) => prev + 1);
  };

  const handlePrev = () => {
    setNav((prev) => prev - 1);
  };

  const getDayDate = (eventdate) => {
    const date = eventdate.toString().split("T");

    const dateFormat = formatDate(date[0]);

    return {
      date: date[0],
      day: dateFormat,
    };
  };

  return (
    <>
      <div id="container">
        <CalendarHeader
          dateDisplay={dateDisplay}
          onNext={handleNext}
          onBack={handlePrev}
        />
        <div className={classes.calendarContainer}>
          <WeekDays />
          <Days />
        </div>
      </div>
    </>
  );
};

export default Calendar;
