import { Hidden } from "@material-ui/core";
import React, { useEffect, useRef, useState, useCallback } from "react";
import Scheduler from "../../Components/CalendarDashboard/Scheduler/Scheduler";
import SidebarContainer from "../../Components/CalendarDashboard/SidebarContainer/SidebarContainer";
import BottomNavigation from "../../shared-components/mobile/BottomNavigation/BottomNavigation";
import { NavbarContainer } from "../../shared-components/mobile";
import { useBusiness } from "../../contexts/BusinessProvider";
import classes from "./CalendarDashboard.module.css";

import Sidebar from "../../Components/Sidebar/Sidebar";
import { useSidebar } from "../../contexts/SidebarProvider/SidebarProvider";

// api imports
import { fetchSetupInfo } from "../../api/SocialMediaSetup";

// contexts imports
import { useToastMessage } from "../../contexts";
import { useLoader } from "../../contexts/LoaderContext";

const CalendarDashboard = ({ location, history, ...props }) => {
  const {
    switchBusiness,
    selectedBusiness,
    allBusiness,
    getAllUserBusiness,
    fetchDefaultBusiness,
  } = useBusiness();

  const [isRedirecting, setIsRedirecting] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  const {
    activeMenu,
    closeActiveMenu,
    openMenu,
    openSubMenu,
    catIcon,
    catColor,
    menuCategory,
    mainMenu,
    gradientColor,
    businessId,
    menuComponents,
  } = useSidebar();

  // contexts
  const { setErrorMessage } = useToastMessage();
  const { startLoader, cancelLoader } = useLoader();

  /*   const [states, setStates] = useState({
    toggled: location.hash === "#open" ? true : false,
    selectedLead: null,
    toggle_DropDown: false,
  }); */

  const [state, setState] = useState({
    toggled: location.hash === "#open" ? true : false,
    selectedLead: null,
    toggle_DropDown: false,
  });

  const toggleBar = () => {
    if (state.toggled) {
      history.push("#close");
    }
    if (!state.toggled) {
      history.push("#open");
    }
    return setState({ ...state, toggled: !state.toggled });
  };

  const [currentPath] = useState(location.pathname);

  const switchBetweenBusiness = (e) => {
    if (selectedBusiness.id === e.target.value) {
      return;
    }

    const business = allBusiness.find(
      (data) => data.businessId === e.target.value
    );

    if (business) {
      switchBusiness(e.target.value, business);
      const urlSplit = location.pathname.split("/");
      const productName = urlSplit[1];
      if (business.plan_category.includes(productName)) {
        const menu = menuCategory.filter(
          (menu) => menu.productName === productName
        );
        const submenu = menu[0]["menu"].filter(
          (sub) => sub.url === "/curate/calendar"
        );
        if (
          (submenu[0].publishOnBusiness.length === 0 ||
            submenu[0].includes(business.businessId)) &&
          submenu[0].published === true
        ) {
          window.location.reload();
        } else {
          window.location.href = business.redirectUrl;
        }
      } else {
        window.location.href = business.redirectUrl;
      }
    }
  };

  /*   const toggleBar = () => {
    if (states.toggled) {
      history.push("#close");
    }
    if (!states.toggled) {
      history.push("#open");
    }
    return setStates({ ...states, toggled: !states.toggled });
  };
 */
  const loadDataRef = useRef(() => {
    !selectedBusiness && fetchDefaultBusiness();
    allBusiness.length < 1 && getAllUserBusiness();
  });

  useEffect(() => {
    loadDataRef.current();
  }, []);

  // callback definition
  const socialSetupInfo = useCallback(async () => {
    startLoader();
    if (selectedBusiness && selectedBusiness.id) {
      try {
        const { connectedAccounts } = await fetchSetupInfo();

        setIsLoading(false);
        cancelLoader();

        if (!connectedAccounts.length) {
          setIsRedirecting(true);
          return history.push("/getting-started");
        }
      } catch (error) {
        cancelLoader();

        const { message, error: err } = error;

        if (message) {
          setErrorMessage(message);
        } else if (err) {
          setErrorMessage(err);
        } else {
          setErrorMessage(error);
        }
      }
    }
  }, [history, selectedBusiness]);

  useEffect(() => {
    socialSetupInfo();
  }, [socialSetupInfo]);

  return isRedirecting
    ? null
    : !isLoading && (
        <>
          <Hidden smUp implementation="js">
            <NavbarContainer
              open={state.toggled}
              //isLoading={isLoading}
              switchBusiness={switchBetweenBusiness}
              toggleBar={toggleBar}
            />
          </Hidden>

          <div className={classes.Container}>
            <div className={classes.inner}>
              {/*         <SidebarContainer
          allBusiness={allBusiness}
          selectedBusiness={selectedBusiness}
          toggleBar={toggleBar}
          open={states.toggled}
          className={classes.Sidebar}
          location={location}
          isLoading={false}
          switchBusiness={switchBetweenBusiness}
        /> */}
              <Hidden xsDown implementation="js">
                <Sidebar
                  switchBetweenBusiness={switchBetweenBusiness}
                  location={location}
                  selectedBusiness={selectedBusiness}
                  business={allBusiness}
                  open={state.toggled}
                  handleClose={toggleBar}
                  className={classes.Sidebar}
                />
              </Hidden>

              <div className={classes.Content}>
                {/* Mobile Only */}
                {/*           <NavbarContainer
            toggleBar={toggleBar}
            open={states.toggled}
            switchBusiness={switchBusiness}
            isLoading={false}
          /> */}
                <Scheduler />
              </div>
            </div>

            <Hidden implementation="js" smUp>
              <BottomNavigation />
            </Hidden>
          </div>
        </>
      );
};

export default CalendarDashboard;
