import React, { useState, useEffect } from "react";
import LeftArrow from "../../../assets/icons/leftArrow.svg";
import RightArrow from "../../../assets/icons/rightArrow.svg";

import EyeIcon from "../../../assets/icons/eye.svg";
import EyeOffIcon from "../../../assets/icons/eyeoff.svg";
import StarIcon from "../../../assets/icons/GrayStar.svg";
import classes from "./calendarHeader.module.css";
import { useSelector } from "react-redux";
import Strategy from "../Strategy/";

import ShowPost from "../ShowPost/showPost";

import ViewEvent from "../ViewEvent/viewEvent";
export default function CalendarHeader({ onNext, onBack, dateDisplay }) {
  const [showEvents, setShowEvents] = useState(false);
  const [showStrategy, setShowStrategy] = useState(false);
  const [eyeOpenIcon, setEyeOpenIcon] = useState(true);
  const strategyState = useSelector((state) => state.strategyState);

  const [showPost, setShowPost] = useState(false);

  useEffect(() => {
    let allSetToTrue = true;
    if (strategyState.strategyStatus) {
      for (const value of Object.keys(strategyState.strategyStatus)) {
        if (!strategyState.strategyStatus[value]) {
          allSetToTrue = false;
        }
      }
    }
    setEyeOpenIcon(allSetToTrue);
  }, [strategyState]);

  return (
    <div className={classes.dateBarWrapper}>
      <div className={classes.calendarWrapper}>
        <span onClick={onBack} style={{ marginTop: 10 }}>
          <img
            src={LeftArrow}
            alt="left"
            // onClick={onBack}
            className={classes.prevBtn}
          />
        </span>

        <div className={classes.dateTitle}>{dateDisplay}</div>

        <span onClick={onNext} style={{ marginTop: 10 }}>
          <img
            src={RightArrow}
            alt="right"
            // onClick={onNext}
            className={classes.nextBtn}
          />
        </span>
      </div>
      <div className={classes.eventsWrapper}>
        <div className={classes.showStrategy}>
          <div
            className={classes.primaryButtonWrapperGap}
            onClick={() => setShowStrategy(true)}
          >
            <div>
              {eyeOpenIcon ? (
                <img src={EyeIcon} alt="visibility" />
              ) : (
                <img src={EyeOffIcon} alt="visibility hidden" />
              )}
            </div>
            <div className={classes.primaryButtonTitle}>Show Strategy</div>
          </div>
        </div>

        <div
          className={classes.primaryButtonWrapper}
          onClick={() => setShowEvents(true)}
        >
          <div>
            <img src={StarIcon} alt="star" />
          </div>
          <div className={classes.primaryButtonTitle}>View Events</div>
        </div>
      </div>
      <ViewEvent open={showEvents} onClose={() => setShowEvents(false)} />

      <Strategy open={showStrategy} onClose={() => setShowStrategy(false)} />

      <ShowPost open={showPost} onClose={() => setShowPost(false)} />
    </div>
  );
}
