import React, { useEffect } from "react";
import classes from "./day.module.css";
import Post from "../Post/post";
import GrayStar from "../../../assets/icons/GrayStar.svg";

import GoldStar from "../../../assets/icons/star.svg";
import { useSelector } from "react-redux";

const Day = ({ day, onClick, index }) => {
  const strategyState = useSelector((state) => state.strategyState);

  let dayValue;

  if (day.value) {
    if (String(day.value).length === 1) {
      dayValue = "0" + day.value;
    } else if (String(day.value).length > 1) {
      dayValue = day.value;
    } else {
      dayValue = "";
    }
  }

  return (
    <div
      className={[classes.day, day.isPadding ? classes.padding : null].join(
        " "
      )}
    >
      <div
        className={[
          classes.date,
          day.isPadding
            ? classes.paddingDate
            : day.isCurrentDay
            ? classes.currentDay
            : null,
        ].join(" ")}
      >
        <div> {dayValue}</div>

        {day.globalEvent.length > 0 &&
          day.globalEvent[0].isGlobal &&
          !day.globalEvent[0].isManual && (
            <img
              src={GrayStar}
              alt="global event indicator"
              className={classes.grayStar}
            />
          )}
        {day.globalEvent.length > 0 && day.globalEvent[0].isManual && (
          <img
            src={GoldStar}
            alt="User important event indicator"
            className={classes.grayStar}
          />
        )}
      </div>
      {day.event.length > 0 &&
        day.event.map((e) => {
          return (
            // strategyState.strategyStatus[
            //   e.event.strategy.strategyID ?? e.event.strategy.name
            // ] && (
            strategyState.strategyStatus[e.event.strategy.name] && (
              <Post
                key={e.postID}
                day={e.event.day}
                postID={e.postID}
                eventTitle={e.event.strategy.name}
                eventColor={e.event.strategy.color}
                eventImage={e.postDetails.postImage}
                isDraft={e.event.isDraft}
                eventDesc={e.postDetails.postDesc}
                imgURL={e.postDetails.postImage}
                isLinkedin={
                  e.postDetails.linkedinDate || e.postDetails.linkedinTime
                }
                isFacebook={
                  e.postDetails.facebookDate || e.postDetails.facebookTime
                }
                isInstagram={
                  e.postDetails.instagramDate || e.postDetails.instagramTime
                }
                isTwitter={
                  e.postDetails.twitterDate || e.postDetails.twitterTime
                }
                postDetails={e.postDetails}
                eventSubTitle={"This is sample subtitle"}
                post={e.post}
                // eventSubTitle={
                //   e.event.isDraft
                //     ? `Drafted @ ${e.postDetails.postTime}`
                //     : `Posted @ ${e.postDetails.postTime}`
                // }
              />
            )
          );
        })}

      {day.event && <div className={classes.event}>{day.event.title}</div>}
    </div>
  );
};

export default Day;
