import { useState, useEffect } from "react";

import Calendar from "../Calendar/Calendar";
import CalendarMobile from "../Mobile/CalendarMobile/calendarMobile";
import PostCreate from "../PostCreate/postCreate";

import { isMobile } from "react-device-detect";
import classes from "./scheduler.module.css";
import { CustomButton } from "../../exports";

// strategy request helper
import { FetchBusinessStrategiesAll } from "../../../api/PostStrategy";

// actions
import { fetchStrategies } from "../../../Store/calendarDashboard/strategy/strategySlice.js";

// dispatcher
import { useDispatch } from "react-redux";

// contexts
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useBusiness } from "../../../contexts/BusinessProvider";

export default function Scheduler() {
  const [createPost, setCreatePost] = useState(false);

  const dispatch = useDispatch();

  const { setErrorMessage } = useToastMessage();
  const { selectedBusiness } = useBusiness();

  useEffect(() => {
    console.log("hii4");
    if (selectedBusiness && selectedBusiness.id) {
      FetchBusinessStrategiesAll()
        .then((res) => {
          if (!res.success) {
            return console.log("Fetch strategy error: ", res.message);
          }

          const toDispatchStrategies = res.payload.map((strategy) => {
            return {
              name: strategy.name,
              strategyID: strategy.id,
              color: strategy.color,
              description: strategy.description,
              businessID: strategy.businessID,
              isDefault: strategy.name === "Event",
              type: strategy.strategyType,
            };
          });

          dispatch(fetchStrategies({ data: toDispatchStrategies }));
        })
        .catch((err) => {
          const { message, error } = err;

          if (message) {
            setErrorMessage(message);
          } else if (error) {
            setErrorMessage(error);
          } else {
            setErrorMessage(err);
          }
        });
    }
    // eslint-disable-next-line
  }, [selectedBusiness]);

  useEffect(() => {
    // const businessID = "6064538f2f94727c34a4d6a9";
    // FetchBusinessStrategiesAll(businessID)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      {!isMobile && (
        <div className={classes["dashboardWrapper"]}>
          <div className={classes["dashboardHeading"]}>
            <div className={classes["dashboardHeadingTitle"]}>Scheduling</div>
            <div>
              <CustomButton
                className={classes["create-post-button"]}
                onClick={() => setCreatePost(true)}
              >
                Create Post
              </CustomButton>
            </div>
          </div>
          <hr className={classes["dividerLine"]} />

          <Calendar />
          <PostCreate open={createPost} onClose={() => setCreatePost(false)} />
        </div>
      )}

      {isMobile && <CalendarMobile />}
    </>
  );
}
